import React, { useEffect, useRef, useState } from 'react';
import '../styles/IDEPage.css';

interface Mission {
  id: string;
  name: string;
  description: string;
  startCode: string;
  testCases: Array<{ input: string; expected: string }>;
  points: number;
}

const missions: Mission[] = [
  {
    id: 'var-101',
    name: ' ',
    description: '  ',
    startCode: 'public class Solution {\n  public static void main(String[] args) {\n    int x = 10;\n    System.out.println(x);\n  }\n}',
    testCases: [{ input: '', expected: '10' }],
    points: 500,
  },
  {
    id: 'type-102',
    name: ' ',
    description: '   ',
    startCode: 'public class Solution {\n  public static void main(String[] args) {\n    String name = "";\n    int age = 30;\n    double height = 165.5;\n    System.out.println(name + " " + age + " " + height);\n  }\n}',
    testCases: [{ input: '', expected: ' 30 165.5' }],
    points: 500,
  },
  {
    id: 'math-103',
    name: ' ',
    description: '  ',
    startCode: 'public class Solution {\n  public static void main(String[] args) {\n    int a = 10;\n    int b = 3;\n    System.out.println(a + b);\n    System.out.println(a * b);\n  }\n}',
    testCases: [{ input: '', expected: '13\n30' }],
    points: 600,
  },
  {
    id: 'if-104',
    name: 'If ',
    description: '  ',
    startCode: 'public class Solution {\n  public static void main(String[] args) {\n    int score = 85;\n    if (score >= 80) {\n      System.out.println("");\n    } else {\n      System.out.println("");\n    }\n  }\n}',
    testCases: [{ input: '', expected: '' }],
    points: 700,
  },
  {
    id: 'loop-105',
    name: 'For ',
    description: '   ',
    startCode: 'public class Solution {\n  public static void main(String[] args) {\n    for (int i = 0; i < 5; i++) {\n      System.out.println(i);\n    }\n  }\n}',
    testCases: [{ input: '', expected: '0\n1\n2\n3\n4' }],
    points: 700,
  },
  {
    id: 'list-106',
    name: ' ',
    description: ' ',
    startCode: 'import java.util.ArrayList;\npublic class Solution {\n  public static void main(String[] args) {\n    ArrayList<String> fruits = new ArrayList<>();\n    fruits.add("");\n    fruits.add("");\n    fruits.add("");\n    System.out.println(fruits.get(0));\n    fruits.add("");\n    System.out.println(fruits.size());\n  }\n}',
    testCases: [{ input: '', expected: '\n4' }],
    points: 700,
  },
];

const IDEPage: React.FC = () => {
  const [activeTab, setActiveTab] = useState<'missions' | 'editor' | 'progress'>('missions');
  const [selectedMissionId, setSelectedMissionId] = useState<string>(missions[0].id);
  const [code, setCode] = useState<string>(missions[0].startCode);
  const [output, setOutput] = useState<string>('');
  const [error, setError] = useState<string>('');
  const [isRunning, setIsRunning] = useState<boolean>(false);
  const [completedMissions, setCompletedMissions] = useState<Set<string>>(new Set());
  const [totalPoints, setTotalPoints] = useState<number>(0);
  const [nookMessage, setNookMessage] = useState<string>('!  Java    ? ');
  const [javaReady, setJavaReady] = useState<boolean>(true);

  // Java IDE      
  useEffect(() => {
    setNookMessage(' ! ');
  }, []);

  const currentMission = missions.find((m) => m.id === selectedMissionId);

  const handleMissionSelect = (missionId: string) => {
    setSelectedMissionId(missionId);
    const mission = missions.find((m) => m.id === missionId);
    if (mission) {
      setCode(mission.startCode);
      setOutput('');
      setError('');
    }
  };

  const runCode = async () => {
    if (!javaReady) {
      setError('Java  .');
      return;
    }

    setIsRunning(true);
    setError('');
    setOutput('');

    try {
      // Send Java code to backend for compilation and execution
      const response = await fetch('/api/java/execute', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          code: code,
          timeout: 5000, // 5 seconds timeout
        }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Java  ');
      }

      const result = await response.json();
      const finalOutput = result.output && result.output.trim() ? result.output.trim() : '( )';
      setOutput(finalOutput);

      // Check if passed test cases
      if (currentMission && finalOutput && finalOutput !== '( )' && finalOutput === currentMission.testCases[0].expected.trim()) {
        if (!completedMissions.has(currentMission.id)) {
          const newCompleted = new Set(completedMissions);
          newCompleted.add(currentMission.id);
          setCompletedMissions(newCompleted);
          setTotalPoints(totalPoints + currentMission.points);
          setNookMessage(`! "${currentMission.name}" ! +${currentMission.points} `);
        }
      } else if (currentMission) {
        setNookMessage(' .    ! ');
      }
    } catch (err) {
      setError((err as Error).message);
      setNookMessage('  .  ! ');
    } finally {
      setIsRunning(false);
    }
  };

  const handleResetCode = () => {
    if (currentMission) {
      setCode(currentMission.startCode);
      setOutput('');
      setError('');
    }
  };

  return (
    <div className="ide-container">
      {/* NookPhone Smartphone UI */}
      <div className="nookphone-wrapper">
        <div className="nookphone-container">
          {/* Header */}
          <div className="ide-header">
            <h2> Java IDE v3.0</h2>
            <div className="header-buttons">
              <button className="header-btn"></button>
              <button className="header-btn"></button>
              <button className="header-btn"></button>
            </div>
          </div>

          {/* Tab Navigation */}
          <div className="ide-tabs">
            <button
              className={`tab-btn ${activeTab === 'missions' ? 'active' : ''}`}
              onClick={() => setActiveTab('missions')}
            >
              
            </button>
            <button
              className={`tab-btn ${activeTab === 'editor' ? 'active' : ''}`}
              onClick={() => setActiveTab('editor')}
            >
              
            </button>
            <button
              className={`tab-btn ${activeTab === 'progress' ? 'active' : ''}`}
              onClick={() => setActiveTab('progress')}
            >
              
            </button>
          </div>

          {/* Content Area */}
          <div className="ide-content">
            {/* Missions Tab */}
            {activeTab === 'missions' && (
              <div className="missions-panel">
                <h3> </h3>
                <div className="missions-list">
                  {missions.map((mission) => (
                    <div
                      key={mission.id}
                      className={`mission-item ${selectedMissionId === mission.id ? 'selected' : ''} ${
                        completedMissions.has(mission.id) ? 'completed' : ''
                      }`}
                      onClick={() => handleMissionSelect(mission.id)}
                    >
                      <div className="mission-header">
                        <h4>{mission.name}</h4>
                        {completedMissions.has(mission.id) && <span className="badge"></span>}
                      </div>
                      <p className="mission-desc">{mission.description}</p>
                      <span className="mission-points">{mission.points}</span>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Editor Tab */}
            {activeTab === 'editor' && (
              <div className="editor-panel">
                <h3>{currentMission?.name || ' '}</h3>
                <p className="mission-desc">{currentMission?.description}</p>

                <div className="editor-section">
                  <label> :</label>
                  <textarea
                    className="code-editor"
                    value={code}
                    onChange={(e) => setCode(e.target.value)}
                    placeholder="Java  ..."
                  />
                </div>

                <div className="editor-controls">
                  <button className="btn-primary" onClick={runCode} disabled={isRunning || !javaReady}>
                    {isRunning ? ' ...' : ''}
                  </button>
                  <button className="btn-secondary" onClick={handleResetCode}>
                    
                  </button>
                </div>

                {output && (
                  <div className="output-section">
                    <label> :</label>
                    <div className="output-box">{output}</div>
                  </div>
                )}

                {error && (
                  <div className="error-section">
                    <label>:</label>
                    <div className="error-box">{error}</div>
                  </div>
                )}
              </div>
            )}

            {/* Progress Tab */}
            {activeTab === 'progress' && (
              <div className="progress-panel">
                <h3></h3>
                <div className="stats">
                  <div className="stat-item">
                    <label> :</label>
                    <span className="stat-value">{totalPoints}</span>
                  </div>
                  <div className="stat-item">
                    <label> :</label>
                    <span className="stat-value">
                      {completedMissions.size} / {missions.length}
                    </span>
                  </div>
                </div>

                <div className="progress-bar-container">
                  <div
                    className="progress-bar-fill"
                    style={{
                      width: `${(completedMissions.size / missions.length) * 100}%`,
                    }}
                  />
                </div>

                <h4> :</h4>
                <ul className="completed-missions">
                  {missions.map((mission) =>
                    completedMissions.has(mission.id) ? (
                      <li key={mission.id}>
                        <span className="check-mark"></span> {mission.name} (+{mission.points})
                      </li>
                    ) : null
                  )}
                </ul>

                {completedMissions.size === 0 && <p className="no-completed">   .</p>}
              </div>
            )}
          </div>

          {/* Nook Character */}
          <div className="nook-section">
            <div className="nook-character">
              <div className="nook-head">
                <div className="nook-eye"></div>
                <div className="nook-eye"></div>
                <div className="nook-nose"></div>
              </div>
              <div className="nook-body"></div>
            </div>
            <div className="nook-message">{nookMessage}</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default IDEPage;
